.SideContent{
  height: 100%;
  width: 326px;
  top: 0px;
  bottom: 0px;
  left: 732px;
}

.IntKeyWords{
  position: relative;
  width: 292px;
  height: 100px;
  left: 25px;
  top: 13px;

  background: var(--dark-gray);
  border-radius: 10px;
  
  p{
    color: var(--white);
    width: 292px;
    height: 40px;
    font-family: 'Museo For Dell';
    font-size: 15px;
    line-height: 18px;
    padding: 11px 0px 0px 15px;
  }
}

.AllKeyWords{
  display: inline-flex;
  flex-wrap: wrap;
}

.SugKeyWords{
  font-family: "Trebuchet";
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 0px 10px 0px 10px;
  height: 21px;
  border: 1px solid;
  border-radius: 10px;
  left: 15px;
  margin-right: 10px;
  margin-bottom: 5px;
}

.MostLanguages{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;

  position: relative;
  width: 292px;
  height: 200px;
  top: 25px;

  background: var(--dark-gray);
  border-radius: 10px;
  left: 25px;
}

.LanguagesIntro{
  width: 292px;
  height: 80px;
  border-bottom: 1px solid var(--dell-border);
}

.LanguagesTittle{
  position: relative;
  width: 292px;
  height: 18px;
  top: 10px;
  padding-left: 15px;
  font-family: 'Museo For Dell';
  font-style: normal;
  font-size: 15px;
  display: flex;
  align-items: center;
  color: var(--white);
}

.LanguagesSubTittle{
  position: relative;
  width: 292px;
  height: 16px;
  color: var(--light-gray);
  padding-left: 15px;
  top: 10px;
}

.LanguagesContent{
  display: flex;
  flex-direction: column;
  height: 259px;
  width: 292px;
}

.NumberLanguage{
  padding: 5px 0px 0px 0px; 
  width: 100%;
  height: 35px;
  display: flex;
}

.InfoLanguage{
  display: flex;
  flex-direction: column;
  position: relative;
}

.LanguageName{
  position: relative;
  display: flex;
  height: 50%;
  width: 292px;
  align-items: end;
  color: var(--white);
  padding-left: 15px;
}

.LangPercentage{
  position: absolute;
  font-family: 'Trebuchet';
  font-style: normal;
  right: 15px;
  align-items: center;
}

.IconPercentage{
  position: relative;
  top: 2px;
  right: 2px;
  color: var(--blue);
}

.DoneProjects{
  position: relative;
  height: 50%;
  width: 292px;
  font-family: 'Trebuchet';
  font-style: normal;
  color: var(--light-gray);
  padding-left: 15px;
}