.SideContent{
  height: 100%;
  width: 326px;
  top: 0px;
  bottom: 0px;
  left: 732px;
}

.IntKeyWords{
  position: relative;
  width: 292px;
  height: 130px;
  left: 25px;
  top: 13px;

  background: var(--dark-gray);
  border-radius: 10px;
  
  p{
    color: var(--white);
    width: 292px;
    height: 40px;
    font-family: 'Museo For Dell';
    font-size: 15px;
    line-height: 18px;
    padding: 11px 0px 0px 15px;
  }
}

.AllKeyWords{
  display: inline-flex;
  flex-wrap: wrap;
}

.SugKeyWords{
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 0px 10px 0px 10px;
  height: 21px;
  color: var(--light-gray);
  border: 1px solid var(--light-gray);
  border-radius: 10px;
  left: 15px;
  margin-right: 10px;
  margin-bottom: 5px;
}

.Ranking{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;

  position: relative;
  width: 292px;
  height: 250px;
  top: 25px;

  background: var(--dark-gray);
  border-radius: 10px;
  left: 25px;
}

.RankingIntro{
  width: 292px;
  height: 55px;
  border-bottom: 1px solid var(--dell-border);
}

.RankingTittle{
  position: relative;
  width: 292px;
  height: 18px;
  top: 10px;
  padding-left: 15px;
  font-family: 'Museo For Dell';
  font-style: normal;
  font-size: 15px;
  display: flex;
  align-items: center;
  color: var(--white);
}   

.InfoIcon{
  position: absolute;
  right: 15px;
  color: var(--light-gray);
}

.RankingSubTittle{
  position: relative;
  width: 292px;
  height: 16px;
  color: var(--light-gray);
  padding-left: 15px;
  top: 10px;
} 

.timeRanking{
  color: var(--light-gray);
  font-family: 'Trebuchet';
  font-style: normal;
  font-size: 11px;
  position: relative;
  left: 15px;
}

.CircleIcon{
  position: relative;
  left: 10px;
  color: var(--light-gray);
}

.RankingContent{
  display: flex;
  flex-direction: column;
  height: 259px;
  width: 292px;
}

.MoreRanking{
  width: 292px;
  display: flex;
  justify-content: center;
  color: var(--light-gray);
}

.NoRanking{
  color: var(--white);
} 

.RankingPlace{
  padding: 5px 0px 0px 8px; 
  width: 292px;
  height: 45px;
  display: flex;
}

.SpanPlaceImg{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;

  img{
    width: 100%;
    height: 100%;
  }
}

.SpanProfileImg{
  width: 35px;
  height: 35px;
  border-radius: 40px;
  overflow: hidden;
  margin-right: 5px;

  img{
    width: 100%;
    height: 100%;
  }
}

.InfoProfile{
  display: flex;
  flex-direction: column;
}

.ProfileName{
  position: relative;
  display: flex;
  height: 50%;
  width: 209px;
  align-items: end;
  color: var(--white);
} 

.ProfilePoints{
  position: absolute;
  font-family: 'Trebuchet';
  font-style: normal;
  color: var(--light-gray);
  right: 12px;
}

.DoneProjects{
  position: relative;
  height: 50%;
  width: 209px;
  font-family: 'Trebuchet';
  font-style: normal;
  color: var(--light-gray);
}